<template>
    <yd-layout>
        <div class="list">
            <div class="listOne">
                <div class="top">
                    <div class="title" v-text="listOne.title"></div>
                    <!-- <div class="time" v-text="listOne.pubTime"></div> -->
                </div>
                <div class="top2">
                    <!-- <div class="title" v-text="listOne.title"></div> -->
                    <div class="time" v-text="formatDate(listOne.pubTime,'yyyy-MM-dd')"></div>
                </div>
                <div class="text" v-html="listOne.html"></div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    name: 'versionHistory',
    data() {
        return {
            listOne: {},
            index: this.$route.params.index
        }
    },
    mounted() {
        // 8月16号上线
        document.setTitle('新功能');
        let vm = this;
        vm.messageList(vm.index);
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        messageList(index) {
            let vm = this;
            vm.get(vm.conf.api + '/AddressBook/notice/messageList', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                console.log(res.messageList[index]);
                vm.listOne = res.messageList[index];
            });
        }
    }
}
</script>
<style lang="less" scoped>
.list {
    background-color: #fff;
    min-height: 100%;
    .listOne {
        min-height: 2.78rem;
        border-bottom: 1px solid #eaeaea;
        padding: .4rem .42rem;
        .top {
            display: flex;
            justify-content: space-between;
        }
        .time {
            font-size: .24rem;
            color: #999;
        }
        .title {
            font-size: .3rem;
            font-weight: bold;
            word-break: break-word;
        }
        .text {
            font-size: .28rem;
            line-height: .5rem;
            margin-top: .3rem;
            word-break: break-all;
            img {
                width: 100%;
            }
        }
    }
    .listOne:last-child {
        border-bottom: none;
    }
}

.f_img {
    height: 2.96rem;
    width: 100%;
    margin-bottom: .3rem;
}

.top2 {
    margin-top: .3rem;
}
</style>


